.block-newsletter {
  width: 100%;
  padding: 110px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.block-newsletter__inner {
  width: 100%;
  padding: 0 30px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
}
@media (min-width: 768px) {
  .block-newsletter__inner {
    padding: 0 15px;
  }
}
.block-newsletter__inner .heading {
  width: 100%;
  margin-bottom: 40px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 50px;
}
@media (min-width: 768px) {
  .block-newsletter__inner .heading {
    margin-bottom: 60px;
  }
}
.block-newsletter__inner .heading .block-title {
  color: rgb(var(--primary-color));
}
.block-newsletter__inner .heading .block-title span,
.block-newsletter__inner .heading .block-title strong {
  color: rgb(var(--secondary-color));
}
.block-newsletter .form {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 60px;
}
.block-newsletter .form-content {
  min-width: 280px;
  flex: 1 0 30%;
  color: rgb(var(--text-color));
}
.block-newsletter .form .wpforms-container {
  min-width: 280px;
  margin-bottom: 0;
  flex: 1 0 calc(70% - 60px);
}
.block-newsletter .form .wpforms-container .wpforms-form {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 15px;
}
.block-newsletter .form .wpforms-container .wpforms-form .wpforms-field-container {
  min-width: 280px;
  flex: 0 1 600px;
  display: flex;
}
.block-newsletter .form .wpforms-container .wpforms-form .wpforms-submit-container {
  margin-top: 0;
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.block-newsletter .form .wpforms-container .wpforms-form .wpforms-submit-container [type=submit] {
  position: relative;
  padding: 10px 35px;
  padding-right: 75px;
  background-color: rgb(var(--primary-color));
}
.block-newsletter .form .wpforms-container .wpforms-form .wpforms-submit-container [type=submit]::after {
  content: url("../../assets/images/paper-plane-solid.svg");
  height: 16px;
  width: 16px;
  position: absolute;
  top: 50%;
  right: 35px;
  transform: translateY(-50%);
  display: block;
  font-size: 16px;
  line-height: 16px;
  fill: #fff;
}
.block-newsletter .form .wpforms-container .wpforms-form .wpforms-submit-container [type=submit]:hover {
  background-color: rgb(var(--secondary-color));
}
@media (min-width: 768px) {
  .block-newsletter .form .wpforms-container .wpforms-form .wpforms-submit-container [type=submit] {
    font-size: 1.6rem;
    line-height: 3rem;
  }
}